import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex)
// 初始化一个仓库
const store = new Vuex.Store({
    state: {
        address:'请输入地址'
    },
    //更改
    mutations: {
        pushin(state,h){
            state.address=h
        }
    },
    //访问
    getters:{
        getAddress(state){
            return state.address
        }
    }
})
export default store