<template>
  <van-list
  class='van-clearfix'
   v-model="loading"
   :finished="finished" 
   finished-text="没有更多了" 
   @load="getData">
    <van-cell 
    v-for="(item,idx) in shopList" 
    :key="idx">
      <van-card price="2.00" :desc="item.address" :title="item.shopName" :thumb="item.picUrl" />
    </van-cell>
  </van-list>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      shopList: []
    };
  },
  async created() {
    
  },
  methods: {
    async getData() {
        let poilist = await axios.post(
          "https://www.easy-mock.com/mock/5d419c967118b96594f4498e/list/poilist"
        );
        this.shopList = [...this.shopList, ...poilist.data.data.shopList];
        console.log(1)
        // 加载状态结束
        this.loading = false;
        // 数据全部加载完成
        if (this.shopList.length >= 40) {
          this.finished = true;
        }
    }
  }
};
</script>
<style>
.van-list {
  margin-bottom: 50px;
}
</style>
